/* eslint-disable no-param-reassign */
export function getLocale(i18n) {
  const urlSearchParams = new URLSearchParams(window.location.search);
  const lang = urlSearchParams.get('lang') || localStorage.getItem('lang') || window.navigator.language;

  switch (lang) {
    case 'en':
      i18n.locale = 'en';
      break;
    case 'pt':
      i18n.locale = 'pt';
      break;
    default:
      i18n.locale = /^pt/i.test(lang) ? 'pt' : 'en';
      break;
  }
  return i18n;
}

export default getLocale;
