<template>
  <v-app>
    <Navbar />
    <v-main>
      <!-- Provides the application the proper gutter -->
      <Header />
      <WhoAmI />
      <Servces />
      <Portfolio @setproject="setProjectIndex" />
      <Footer />
    </v-main>
    <Project
      :open="dialogProject"
      @close="toggleDalogProject"
      :project="getProject"
    />
    <Bottom />
  </v-app>
</template>

<script lang="ts">
import Vue from 'vue';
import Navbar from './components/Navbar.vue';
import Bottom from './components/Bottom.vue';
import WhoAmI from './components/sections/WhoAmI.vue';
import Servces from './components/sections/Services.vue';
import Portfolio from './components/sections/Portfolio.vue';
import Footer from './components/sections/Footer.vue';
import Project from './components/Project.vue';
import projects from './projects.json';
import Header from './components/sections/Header.vue';

export default Vue.extend({
  components: {
    Navbar,
    Bottom,
    WhoAmI,
    Servces,
    Portfolio,
    Footer,
    Project,
    Header,
  },
  data: () => ({
    dialogPortfolio: false,
    dialogProject: false,
    projectIndex: 0,
  }),
  computed: {
    getProject() {
      return projects[this.projectIndex];
    },
  },
  methods: {
    toggleDialogPortfolio() {
      this.dialogPortfolio = !this.dialogPortfolio;
    },
    toggleDalogProject() {
      this.dialogProject = !this.dialogProject;
    },
    setProjectIndex(index = 0) {
      this.projectIndex = index;
      this.toggleDalogProject();
    },
  },
});
</script>

<style>
h2,
h3,
h4,
h5 {
  color: #0073b3;
}
html {
  scroll-behavior: smooth;
}
</style>
