<template>
  <v-menu left bottom>
    <template v-slot:activator="{ on, attrs }">
      <v-btn icon color="#2fb5ff" v-bind="attrs" v-on="on" aria-label="Tradutor">
        <v-icon>mdi-translate</v-icon>
      </v-btn>
    </template>

    <v-list>
      <v-list-item v-for="n in locales" :key="n" @click="() => translate(n)">
        <v-list-item-title>{{ n }}</v-list-item-title>
      </v-list-item>
    </v-list>
  </v-menu>
</template>

<script lang="ts">
import Vue from 'vue';

export default Vue.extend({
  data: () => ({
    locales: ['PortugÃªs', 'English'],
  }),
  methods: {
    translate(locale = '') {
      switch (locale) {
        case 'PortugÃªs':
          this.$i18n.locale = 'pt';
          localStorage.setItem('lang', 'pt');
          break;
        case 'English':
          this.$i18n.locale = 'en';
          localStorage.setItem('lang', 'en');
          break;
        default:
          this.$i18n.locale = 'en';
          localStorage.setItem('lang', 'en');
          break;
      }
    },
  },
});
</script>
